import useFetch from "./usefetch";
import { useParams,useNavigate } from "react-router-dom";
import './style/animeDetails.css'
import { motion } from 'framer-motion';


const AnimeDetails = () => {


    const navigate = useNavigate();
    const {id} = useParams()
    const { data, isLoading, error } = useFetch('/api/anime/' + id);
    return ( 
    <>
        {error && <div>{error}</div>}
        {isLoading && <div className='loading'></div>}
            {data && <>
                <div className="details">
                    <motion.button className="back" onClick={()=>navigate('/anime')}
                    whileHover={{scale: 1.2}}>Go Back</motion.button>
                    
                    <a href={data.url} target="_blank" rel="noreferrer" className="malLink">MAL LINK</a>

            <section className="right">
                <div className="animeImage">
                    <img src={data.images.webp.large_image_url} alt={data.title} />
                </div>
                <div className="naming">
                            <h1 className="eng">{data.title_english}</h1>
                    <hr />
                            <h1 className="jap">{data.title_japanese}</h1>
                </div>

                        <div className="stats">
                            <p>Rank:</p> <span>{data.rank == null ? 'not available':data.rank}</span>
                            <p>Score:</p> <span>{data.score == null ? 'not available':data.score}</span>
                            <p>Season:</p> <span>{data.season == null ? 'not available':data.season}</span>
                            <p>Status: </p><span>{data.status == null ? 'not available':data.status}</span>
                            <p>Year:</p> <span>{data.year == null ? 'not available':data.year}</span>
                            <p>Type: </p><span>{data.type == null ? 'not available':data.type}</span>
                            <p>Rating: </p><span>{data.rating == null ? 'not available':data.rating}</span>
                            <p>Episodes:</p> <span>{data.episodes == null ? 'not available':data.episodes}</span>
                            <p>Duration: </p><span>{data.duration == null ? 'not available':data.duration}</span>
                            <p>Airing: </p><span>{data.airing === false ? "finished" : 'airing'}</span>
                            <p>Broadcast on:</p> <span>{data.broadcast.day == null ? 'not available' : data.broadcast.day}</span>
                            <p>Source: </p><span>{data.source == null ? 'not available':data.source}</span>
                           <p className="from">Aired From:</p><span>{data.aired.string == null ? 'not available' : data.aired.string}</span>
    

                </div>
            </section>
            <section className="left-det">
                        <div className="trailer" id="trailer">
                   {data.trailer.embed_url == null ? null : <iframe width="100%" height="315" src={`${data.trailer.embed_url}?autoplay=0`}  title="YouTube video player" frameBorder="0" allow="accelerometer; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowFullScreen></iframe>}
                        </div>
                        <div className="naming">
                            <h1 className="eng">{data.title_english}</h1>
                            <h1 className="eng">{data.title_japanese}</h1>
                        </div>
                <div className="dit">
                            <div className="synopsis">
                                <h1 className="sy">Synopsis</h1>
                    {data.synopsis}
                            </div>
                    <div className="special">
                                <div className="history">
                                    <h1 className="history-head">History</h1>
                            {data.background == null ? 'not available' : data.background}
                                </div>
                                <div className="photos">
                                    <img src={data.images.webp.image_url} alt="" />
                                    <img src={data.images.webp.large_image_url} alt="" />
                                </div>
                   </div>
                </div>
            </section>
       </div>
            </>}
    </>
     );
}
 
export default AnimeDetails;