import useFetch from './usefetch';
import AnimeCard from './animeCard';
import './style/animeList.css'
import {useState} from 'react';



const AnimeList = () => {

    const [url, setUrl] = useState('/api')
    const [name, setName] = useState("random");
    const [current, setCurrent] = useState('filter');

    
    const filterChoice = (e) => {
        setCurrent('Go Back')
        const value = document.getElementById('filter').value;
        setUrl(`/top/${value}`)
        setName(value)
    } 

     const { data: anime, error, isLoading, total } = useFetch(url)
  
    return ( 
        <div className="home">
            <div className="det">
                <h2 className="name">{name}</h2>
                <select name="sort"  className='sort' id="filter" onChange={filterChoice}>
                    <option value="random">{current}</option>
                    <option value="airing">Top Airing</option>
                    <option value="upcoming">Top Upcoming</option>
                    <option value="popularity">Top By popularity</option>

                </select>
                <h3 className='count'>Total : {total}</h3>
            </div>
            <div className="container">
                {error && <div>{error}</div>}
                {isLoading && <div className='loading'></div>}
                {anime && <AnimeCard anime={anime} />}
            </div>
</div>
     );
}
 
export default AnimeList;
